import Vue from 'vue';
//配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.创建组件
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import news from '@/views/modules/news/list'
    import aboutus from '@/views/modules/aboutus/list'
    import gongyijuanzeng from '@/views/modules/gongyijuanzeng/list'
    import jinjichengdu from '@/views/modules/jinjichengdu/list'
    import wupinxinxi from '@/views/modules/wupinxinxi/list'
    import wupinleixing from '@/views/modules/wupinleixing/list'
    import gongyitonggao from '@/views/modules/gongyitonggao/list'
    import systemintro from '@/views/modules/systemintro/list'
    import yonghu from '@/views/modules/yonghu/list'
    import ganxiepingjia from '@/views/modules/ganxiepingjia/list'
    import discusswupinxinxi from '@/views/modules/discusswupinxinxi/list'
    import jiaoyixinxi from '@/views/modules/jiaoyixinxi/list'
    import juanzengleixing from '@/views/modules/juanzengleixing/list'
    import config from '@/views/modules/config/list'
    import discussgongyitonggao from '@/views/modules/discussgongyitonggao/list'


//2.配置路由   注意：名字
const routes = [{
    path: '/index',
    name: '系统首页',
    component: Index,
    children: [{
      // 这里不设置值，是把main作为默认页面
      path: '/',
      name: '系统首页',
      component: Home,
      meta: {icon:'', title:'center'}
    }, {
      path: '/updatePassword',
      name: '修改密码',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '支付',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '个人信息',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/news',
        name: '公告信息',
        component: news
      }
      ,{
	path: '/aboutus',
        name: '关于我们',
        component: aboutus
      }
      ,{
	path: '/gongyijuanzeng',
        name: '公益捐赠',
        component: gongyijuanzeng
      }
      ,{
	path: '/jinjichengdu',
        name: '紧急程度',
        component: jinjichengdu
      }
      ,{
	path: '/wupinxinxi',
        name: '物品信息',
        component: wupinxinxi
      }
      ,{
	path: '/wupinleixing',
        name: '物品类型',
        component: wupinleixing
      }
      ,{
	path: '/gongyitonggao',
        name: '公益通告',
        component: gongyitonggao
      }
      ,{
	path: '/systemintro',
        name: '系统简介',
        component: systemintro
      }
      ,{
	path: '/yonghu',
        name: '用户',
        component: yonghu
      }
      ,{
	path: '/ganxiepingjia',
        name: '感谢评价',
        component: ganxiepingjia
      }
      ,{
	path: '/discusswupinxinxi',
        name: '物品信息评论',
        component: discusswupinxinxi
      }
      ,{
	path: '/jiaoyixinxi',
        name: '交易信息',
        component: jiaoyixinxi
      }
      ,{
	path: '/juanzengleixing',
        name: '捐赠类型',
        component: juanzengleixing
      }
      ,{
	path: '/config',
        name: '轮播图管理',
        component: config
      }
      ,{
	path: '/discussgongyitonggao',
        name: '公益通告评论',
        component: discussgongyitonggao
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '/',
    name: '系统首页',
    redirect: '/index'
  }, /*默认跳转路由*/
  {
    path: '*',
    component: NotFound
  }
]
//3.实例化VueRouter  注意：名字
const router = new VueRouter({
  mode: 'hash',
  /*hash模式改为history*/
  routes // （缩写）相当于 routes: routes
})
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
